import { Component, OnInit } from '@angular/core';
import { Order, BOOK, _Book, _User } from '../../models/cart/order';
import { OrderService } from '../../models/cart/order.service';
import { LoginStatusService } from '../../models/login-logout/login-status.service';
import { UserService } from '../../models/login-logout/user.service';
import { first } from 'rxjs/operators';
import { User } from '../../models/login-logout/user';

@Component({
  selector: 'app-payment-success',
  templateUrl: './payment-success.component.html',
  styleUrls: ['./payment-success.component.css']
})
export class PaymentSuccessComponent implements OnInit {
  statusUser = false;
  currentUser = new User();
  orderCurrent;
  order = new Order()
  constructor(
    private loginStatusService: LoginStatusService,
    private orderService : OrderService,
    private userService: UserService
  ) {
    loginStatusService.status$.subscribe(
      status => {
        // this.status = status;
        if (status) {
          this.getUser()
        }
        // this.currentUser = JSON.parse(localStorage.getItem('_currentUser'));
        // console.log(this.status);
      }
    );
    orderService.totalStatus$.subscribe(
      status => {
        this.orderCurrent = JSON.parse(localStorage.getItem('_orderCurrent'))
        console.log(this.orderCurrent)
      }
    )
   }

  ngOnInit() {
    this.orderCurrent = JSON.parse(localStorage.getItem('_orderCurrent'))
    this.getOrder();
    this.checkToken();
  }
  getOrder() {
    this.orderCurrent = JSON.parse(localStorage.getItem('_orderCurrent'))
    console.log(this.orderCurrent._id)
    this.orderService.getOrder(this.orderCurrent._id).subscribe(
      _ => this.order = _
    )
  }
  getUser(): void {
    this.userService.getAll().pipe(first()).subscribe(_ => this.currentUser = _.user)
  };
  checkToken() {
    if ( localStorage.getItem('_currentUser')) {
      this.statusUser = true;
      this.loginStatusService.setStatus(this.statusUser);
      this.getUser();
    } else {
      this.loginStatusService.setStatus(this.statusUser);
    }
    console.log('check')
  }
}
