import { Injectable, OnInit } from "@angular/core";
import { ShoppingCart } from "./shopping-cart";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Order } from "./order";
import { Observable, Subject } from 'rxjs';
import { User } from "../login-logout/user";
import { UserService } from "../login-logout/user.service";
import { first } from "rxjs/operators";
import { LoginStatusService } from "../login-logout/login-status.service";

const httpOptions = {
    headers: new HttpHeaders({ 'Content-type': 'application/json' })
}

@Injectable()
export class OrderService implements OnInit {
    ordersUrl = 'http://green-web-bookshop.herokuapp.com/api/orders';
    currentUser = new User();
    statusUser = false;
    status: Boolean;
    currentOrder : Order;
    totalSource = new Subject<string>();
    constructor(
        private http: HttpClient,
        private userService: UserService,
        private loginStatusService: LoginStatusService,
    ) {
        loginStatusService.status$.subscribe(
            status => {
                this.status = status;
                    this.getUser()
                console.log(this.status);
                // this.getUser();
            }
        );
    }
    
    
    ngOnInit() {
        this.checkToken();
    }

    totalStatus$ = this.totalSource.asObservable();
    setOrderStatus(order) {
        this.totalSource.next(order);
    }
    getUser(): void {
        this.userService.getAll().subscribe(_ => this.currentUser = _.user);
        console.log(this.currentUser)
    };
    checkToken() {
        if (localStorage.getItem('_currentUser')) {
            this.statusUser = true;
            this.loginStatusService.setStatus(this.statusUser);
            this.getUser();
        } else {
            this.loginStatusService.setStatus(this.statusUser);
        }
        console.log(this.currentUser)
    }
    addOrder(order: Order): Observable<Order> {
        return this.http.post<Order>(this.ordersUrl, order, httpOptions)
    }
    getOrders(): Observable<Order[]> {
        this.checkToken();
        console.log(this.currentUser._id)
        const url = `${this.ordersUrl}/user/${this.currentUser._id}`
        return this.http.get<Order[]>(url)
    }
    getOrder(id: string): Observable<Order> {
        const url = `${this.ordersUrl}/${id}`;
        return this.http.get<Order>(url)
    }
}