import { Component, OnInit, Input } from '@angular/core';
import { Book } from '../../models/book';
import { GenreService } from '../../models/genre.service';
import { Genre } from '../../models/genre';
import { BookService } from '../../models/book.service';
import { Observable } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { ParamMap, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-filter-product',
  templateUrl: './filter-product.component.html',
  styleUrls: ['./filter-product.component.css']
})
export class FilterProductComponent implements OnInit {
  genres : Genre[];
  books$: Observable<Book[]>;
  filterGenres : Book[];
  books : Book[]
  constructor(
    private genreService: GenreService,
    private bookService: BookService,
    private route: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.getGenres();
    this.getBooks();
  }
  getGenres():void {
    this.genreService.getGenres()
      .subscribe( genres => this.genres = genres );
  }
  getBooks(): void {
    this.books$ = this.route.paramMap.pipe(
      switchMap((params: ParamMap) => {
        return this.bookService.getBooks();
      })
    )
  }
  // to use for filteredGenres() because books$ can't filter
  getBooks_() {
this.bookService.getBooks().subscribe(_ => this.books = _)
  }
  filteredGenres(genre: Genre) {
    this.filterGenres = this.books.filter(_ => _.genre._id = genre._id)
  }

}
